import { fetchJson, fetchJsonStream, } from 'spatial-id-svc-base';
/** 空間 ID の範囲内の予約エリアを複数取得する */
export const getReservedAreas = async function* ({ baseUrl, authInfo, payload, abortSignal, }) {
    for await (const chunk of fetchJsonStream({
        method: 'POST',
        baseUrl,
        path: '/area_service/reserved_areas_list',
        authInfo,
        payload,
        abortSignal,
    })) {
        yield chunk;
    }
};
/** ID を指定して予約エリアを 1 件取得する */
export const getReservedArea = async ({ baseUrl, authInfo, id, abortSignal, }) => {
    return await fetchJson({
        method: 'GET',
        baseUrl,
        path: `/area_service/reserved_areas/${encodeURIComponent(id)}`,
        authInfo,
        abortSignal,
    });
};
/** 予約エリアを生成する */
export const createReservedArea = async ({ baseUrl, authInfo, payload, abortSignal, }) => {
    return await fetchJson({
        method: 'POST',
        baseUrl,
        path: '/area_service/reserved_areas',
        authInfo,
        payload,
        abortSignal,
    });
};
/** ID を指定して予約エリアを削除する */
export const deleteReservedArea = async ({ baseUrl, authInfo, id, abortSignal, }) => {
    await fetchJson({
        method: 'DELETE',
        baseUrl,
        path: `/area_service/reserved_areas/${encodeURIComponent(id)}`,
        authInfo,
        abortSignal,
    });
};
