import { fetchJson, fetchJsonStream, } from 'spatial-id-svc-base';
/** 空間 ID の範囲内の割込禁止エリアを複数取得する */
export const getBlockedAreas = async function* ({ baseUrl, authInfo, payload, abortSignal, }) {
    for await (const chunk of fetchJsonStream({
        method: 'POST',
        baseUrl,
        path: '/area_service/blocked_areas_list',
        authInfo,
        payload,
        abortSignal,
    })) {
        yield chunk;
    }
};
/** ID を指定して割込禁止エリアを 1 件取得する */
export const getBlockedArea = async ({ baseUrl, authInfo, id, abortSignal, }) => {
    return await fetchJson({
        method: 'GET',
        baseUrl,
        path: `/area_service/blocked_areas/${encodeURIComponent(id)}`,
        authInfo,
        abortSignal,
    });
};
/** 割込禁止エリアを生成する */
export const createBlockedArea = async ({ baseUrl, authInfo, payload, abortSignal, }) => {
    return await fetchJson({
        method: 'POST',
        baseUrl,
        path: '/area_service/blocked_areas',
        authInfo,
        payload,
        abortSignal,
    });
};
/** 割込禁止エリアを削除する */
export const deleteBlockedArea = async ({ baseUrl, authInfo, id, abortSignal, }) => {
    await fetchJson({
        method: 'DELETE',
        baseUrl,
        path: `/area_service/blocked_areas/${encodeURIComponent(id)}`,
        authInfo,
        abortSignal,
    });
};
/** 空間 ID の範囲内の割込禁止エリアの追加・削除を監視する */
export const watchBlockedAreas = async function* ({ baseUrl, authInfo, payload, abortSignal, }) {
    for await (const chunk of fetchJsonStream({
        method: 'POST',
        baseUrl,
        path: '/area_service/blocked_areas_events',
        authInfo,
        payload,
        abortSignal,
    })) {
        yield chunk;
    }
};
